<template>
  <div class="canvas-main">
    <div>手动福建省代理费京东方两件事砥砺奋进手动福建省代理费京东方两件事砥砺奋进</div>
    <canvas class="canvas-content" ref="canvas" @touchstart="touchstart($event)" @touchmove="move($event)" @touchend="end($event)"></canvas>
  </div>
</template>

<script>
import _ from "underscore"
import dataJson from '@/js/canvasJson'
export default {
  name: 'CanvasTransformComponent',
  props: ['setHead'],
  data () {
    return {
      canvasObj: '',
      mark: 0,
      touch:{
        x: 0,
        y: 0
      }
    }
  },
  mounted () {
    this.horizontal();
  },
  methods: {
    horizontal () {
      let width = document.documentElement.clientWidth,
        height = document.documentElement.clientHeight,
        wrapper = document.getElementById('app'),
        style = '';
      if(width >= height){
        style += 'width:100%;';
        style += 'height:100%;';
        style += '-webkit-transform: rotate(0); transform: rotate(0);';
        style += '-webkit-transform-origin: 0 0;';
        style += 'transform-origin: 0 0;';
      }else{
        style += 'width:'+height+'px;';
        style += 'height:'+width+'px;';
        style += '-webkit-transform: rotate(90deg); transform: rotate(90deg);';
        style += '-webkit-transform-origin: '+width/2+'px '+ width/2 +'px;';
        style += 'transform-origin: '+width/2+'px '+width/2+'px;';
      }
      document.body.style.cssText = style;

      this.$refs.canvas.width = height;
      this.$refs.canvas.height = 300;
      this.canvasObj = this.$refs.canvas.getContext('2d');
      this.mark = document.documentElement.clientWidth - this.$refs.canvas.offsetTop - document.getElementById('app').scrollTop;
    },
    touchstart (e) {
      this.mark += document.getElementById('app').scrollTop;
      this.touch.x = e.touches[0].pageY;
      this.touch.y = this.mark - e.touches[0].pageX;
      // this.moveLine();
    },
    move (e) {
      let x = e.touches[0].pageY,
          y = this.mark - e.touches[0].pageX;

      this.moveLine(x, y);
    },
    end () {

    },
    moveLine (x, y) {
      this.canvasObj.beginPath();
      this.canvasObj.moveTo(this.touch.x, this.touch.y);
      this.canvasObj.lineTo(x, y);
      this.canvasObj.stroke();
      this.touch.x = x;
      this.touch.y = y;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="true">
  .canvas-content{
    border-top: 1px solid red;
    border-bottom: 1px solid red;
  }
</style>



