<template>
  <div>
    <keyboard :id="'1'" :get-input="getInput" :option="option"></keyboard>
    <keyboard :id="'2'" :get-input="getInput" :option="option2"></keyboard>
  </div>
</template>

<script>
import keyboard from '@/components/core/KeyboardComponent'
export default {
  name: 'InputeComponent',
  data () {
    return {
      option: {
        type: 'number'
      },
      option2: {
        type: 'money',
        style: {width: '50%', height: '.5rem'}
      }
    }
  },
  mounted () {
    this.$parent.msg = '输入框'
  },
  methods: {
    getInput (val) {}
  },
  components: {keyboard}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



