<template>
  <div class="kb-box" ref="keyboard_box">
    <!-- <pre v-if="option.type != 'money'" class="kb-input" @click="showBoard">{{val}}</pre> -->
    <!-- <div v-if="option.type == 'money'" class="kb-input" @click="showBoard">{{val}}</div> -->
    <div class="block kb-tem" @click="showBoard">
      <span class="kb-input" ref="keyboard_input">{{val}}</span>
      <i class="kb-flicker" v-show="flicker"></i>
      <div class="kb-clear block" v-show="val>0||val.length>0"></div>
    </div>
    <!-- <div class="zxx_align_box_2"><span class="zxx_align_word">这里显示多行文字。</span></div> -->
  </div>
</template>

<script>
import $ from 'n-zepto'
export default {
  name: 'keyboard',
  props: ['id', 'getInput', 'option'],
  data () {
    return {
      // 输入框标识
      key: '#keyboard_' + this.id,
      // 光标是否展示
      flicker: false,
      // 值
      val: ''
    }
  },
  created () {
    let template = [
      '<div class="keyboard-main" id="keyboard_'+this.id+'" style="display:none;">',
        '<div class="keyboard-layer">',
          '<div class="keyboard-num" style="display: none;">',
            '<div class="keyboard-num-left">',
              '<p data-key="%"><span>%</span></p>',
              '<p data-key="/"><span>/</span></p>',
              '<p data-key="-"><span>-</span></p>',
              '<p data-key="+"><span>+</span></p>',
            '</div>',
            '<ul class="keyboard-num-content">',
              '<li>',
                '<p data-key="1"><span>1</span></p>',
                '<p data-key="2"><span>2</span></p>',
                '<p data-key="3"><span>3</span></p>',
                '<p class="gray" data-key="-1"><span>x</span></p>',
              '</li>',
              '<li>',
                '<p data-key="4"><span>4</span></p>',
                '<p data-key="5"><span>5</span></p>',
                '<p data-key="6"><span>6</span></p>',
                '<p class="gray" data-key=".">.</p>',
              '</li>',
              '<li>',
                '<p data-key="7"><span>7</span></p>',
                '<p data-key="8"><span>8</span></p>',
                '<p data-key="9"><span>9</span></p>',
                '<p class="gray" data-key="@"><span>@</span></p>',
              '</li>',
            '</ul>',
          '</div>',
          '<div class="keyboard-num-footer" style="display: none;">',
            '<p class="gray" data-key="-2"><span>字母</span></p>',
            '<p class="gray" data-key="-4"><span>符号</span></p>',
            '<p data-key="0" class="keyboard-num-zero"><span>0</span></p>',
            '<p class="gray" data-key="-5"><span class="keyboard-num-space"></span></p>',
            '<p class="gray" data-key="-6"><span>&crarr;</span></p>',
          '</div>',
          '<ul class="keyboard-money" style="display: none;">',
            '<li>',
              '<p data-key="1"><span>1</span></p>',
              '<p data-key="2"><span>2</span></p>',
              '<p data-key="3"><span>3</span></p>',
            '</li>',
            '<li>',
              '<p data-key="4"><span>4</span></p>',
              '<p data-key="5"><span>5</span></p>',
              '<p data-key="6"><span>6</span></p>',
            '</li>',
            '<li>',
              '<p data-key="7"><span>7</span></p>',
              '<p data-key="8"><span>8</span></p>',
              '<p data-key="9"><span>9</span></p>',
            '</li>',
            '<li>',
              '<p class="gray" data-key=".">.</p>',
              '<p data-key="0"><span>0</span></p>',
              '<p class="gray" data-key="-1"><span>x</span></p>',
            '</li>',
          '</ul>',
          '<ul class="keyboard-letter" style="display: none;">',
            '<li class="keyboard-lower">',
              '<p data-key="q"><span>q</span></p>',
              '<p data-key="w"><span>w</span></p>',
              '<p data-key="e"><span>e</span></p>',
              '<p data-key="r"><span>r</span></p>',
              '<p data-key="t"><span>t</span></p>',
              '<p data-key="y"><span>y</span></p>',
              '<p data-key="u"><span>u</span></p>',
              '<p data-key="i"><span>i</span></p>',
              '<p data-key="o"><span>o</span></p>',
              '<p data-key="p"><span>p</span></p>',
            '</li>',
            '<li class="keyboard-lower">',
              '<p data-key="a"><span>a</span></p>',
              '<p data-key="s"><span>s</span></p>',
              '<p data-key="d"><span>d</span></p>',
              '<p data-key="f"><span>f</span></p>',
              '<p data-key="g"><span>g</span></p>',
              '<p data-key="h"><span>h</span></p>',
              '<p data-key="j"><span>j</span></p>',
              '<p data-key="k"><span>k</span></p>',
              '<p data-key="l"><span>l</span></p>',
            '</li>',
            '<li class="keyboard-lower">',
              '<p class="gray" data-key="-7"><span>↑</span></p>',
              '<p data-key="z"><span>z</span></p>',
              '<p data-key="x"><span>x</span></p>',
              '<p data-key="c"><span>c</span></p>',
              '<p data-key="v"><span>v</span></p>',
              '<p data-key="b"><span>b</span></p>',
              '<p data-key="n"><span>n</span></p>',
              '<p data-key="m"><span>m</span></p>',
              '<p class="gray" data-key="-1"><span>x</span></p>',
            '</li>',
            '<li class="keyboard-capital">',
              '<p data-key="Q"><span>Q</span></p>',
              '<p data-key="W"><span>W</span></p>',
              '<p data-key="E"><span>E</span></p>',
              '<p data-key="R"><span>R</span></p>',
              '<p data-key="T"><span>T</span></p>',
              '<p data-key="Y"><span>Y</span></p>',
              '<p data-key="U"><span>U</span></p>',
              '<p data-key="I"><span>I</span></p>',
              '<p data-key="O"><span>O</span></p>',
              '<p data-key="P"><span>P</span></p>',
            '</li>',
            '<li class="keyboard-capital">',
              '<p data-key="A"><span>A</span></p>',
              '<p data-key="S"><span>S</span></p>',
              '<p data-key="D"><span>D</span></p>',
              '<p data-key="F"><span>F</span></p>',
              '<p data-key="G"><span>G</span></p>',
              '<p data-key="H"><span>H</span></p>',
              '<p data-key="J"><span>J</span></p>',
              '<p data-key="K"><span>K</span></p>',
              '<p data-key="L"><span>L</span></p>',
            '</li>',
            '<li class="keyboard-capital">',
              '<p class="gray" data-key="-7"><span>↑</span></p>',
              '<p data-key="Z"><span>Z</span></p>',
              '<p data-key="X"><span>X</span></p>',
              '<p data-key="C"><span>C</span></p>',
              '<p data-key="V"><span>V</span></p>',
              '<p data-key="B"><span>B</span></p>',
              '<p data-key="N"><span>N</span></p>',
              '<p data-key="M"><span>M</span></p>',
              '<p class="gray" data-key="-1"><span>x</span></p>',
            '</li>',
            '<li>',
              '<p class="gray" data-key="-3"><span>123</span></p>',
              '<p data-key=",">,</p>',
              '<p data-key="-5"><span class="keyboard-num-space"></span></p>',
              '<p data-key=".">.</p>',
              '<p class="gray" data-key="-6"><span>&crarr;</span></p>',
            '</li>',
          '</ul>',
          '<ul class="keyboard-symbol" style="display: none;">',
            '<li>',
              '<p data-key="@"><span>@</span></p>',
              '<p data-key="#"><span>#</span></p>',
              '<p data-key="$"><span>$</span></p>',
              '<p data-key="%"><span>%</span></p>',
              '<p data-key="^"><span>^</span></p>',
              '<p data-key="&"><span>&</span></p>',
              '<p data-key="*"><span>*</span></p>',
              '<p data-key="("><span>(</span></p>',
              '<p data-key=")"><span>)</span></p>',
            '</li>',
            '<li>',
              '<p data-key="-"><span>-</span></p>',
              '<p data-key="+"><span>+</span></p>',
              '<p data-key="_"><span>_</span></p>',
              '<p data-key="="><span>=</span></p>',
              '<p data-key="["><span>[</span></p>',
              '<p data-key="]"><span>]</span></p>',
              '<p data-key="|"><span>|</span></p>',
              '<p data-key="\\"><span>\\</span></p>',
              '<p data-key=":"><span>:</span></p>',
            '</li>',
            '<li>',
              '<p data-key="↑"><span>↑</span></p>',
              '<p data-key=";"><span>;</span></p>',
              '<p data-key="\""><span>\"</span></p>',
              '<p data-key="\'"><span>\'</span></p>',
              '<p data-key=","><span>,</span></p>',
              '<p data-key="."><span>.</span></p>',
              '<p data-key="/"><span>/</span></p>',
              '<p data-key="?"><span>?</span></p>',
              '<p data-key="~"><span>~</span></p>',
            '</li>',
            '<li>',
              '<p class="gray" data-key="-3"><span>123</span></p>',
              '<p data-key="♂">♂</p>',
              '<p data-key="-5"><span class="keyboard-num-space"></span></p>',
              '<p data-key="♀">♀</p>',
              '<p class="gray" data-key="-6"><span>&crarr;</span></p>',
            '</li>',
          '</ul>',
        '</div>',
      '</div>',
    ].join('');
    if($(this.key).length <= 0){
      // 插入DOM片段
      $('body').append(template);
      this.onEvent();
    }
  },
  mounted () {
    this.initAttribute();
  },
  methods: {
    // 展示键盘
    showBoard () {
      $(this.key).show().find('.keyboard-num,.keyboard-num-footer').show();
      this.$nextTick(function(){
        this.showType();
      })
    },
    onEvent () {
      let dom;
      let now = new Date();
      let max = this.option.max || 10;
      let active = false;
      $(this.key).on('touchstart', (e) => {
        dom = null;
        // 判断是否是点击键盘内
        if($(e.target).parents('.keyboard-num,.keyboard-num-footer,.keyboard-money,.keyboard-letter,.keyboard-symbol').length > 0
          || $(e.target).hasClass('keyboard-num-footer')
          || $(e.target).hasClass('keyboard-num')
          || $(e.target).hasClass('keyboard-money')
          || $(e.target).hasClass('keyboard-letter')
          || $(e.target).hasClass('keyboard-symbol'))
        {
          if(e.target.nodeName == 'P'){
            dom = $(e.target)
          }
          if($(e.target).parent()[0].nodeName == 'P'){
            dom = $(e.target).parent();
          }
          if(dom){
            dom.addClass('touch');
          }
        }else{
          $(this.key).hide();
          this.flicker = false;
        }
      })
      $(this.key).on('touchend', (e) => {
        if(dom){
          let k = dom.data('key');
          dom.removeClass('touch');
          let arr,str;
          if(this.val.length >= max && !(k == -1 || k == -2 || k == -3 || k == -4 || k == -7)){
            return;
          }
          switch(k){
            // 删除
            case -1:
              arr = this.val.split('');
              arr.pop();
              str = arr.join('');
              this.val = str;
              break;
            // 字母
            case -2:
              this.option.type = 'letter';
              this.showType();
              break;
            // 数字
            case -3:
              this.option.type = 'number';
              this.showType();
              break;
            // 符号
            case -4:
              this.option.type = 'symbol';
              this.showType();
              break;
            // 空格
            case -5:
              this.val += ' ';
              break;
            // 换行
            case -6:
              this.val += '\n';
              break;
            // 大小写
            case -7:
              if(active){
                active = false;
                $(this.key).removeClass('active');
              }else{
                active = true;
                $(this.key).addClass('active');
              }
              break;
            // 正常数据
            default:
              this.val += k;
              break;
          }
          this.getInput(this.val);
        }
      })
    },
    // 展示键盘后初始化键盘类型
    showType () {
      this.flicker = true;
      // 数字
      if(this.option.type == 'number'){
        $(this.key).find('.keyboard-money,.keyboard-letter,.keyboard-symbol').hide();
        $(this.key).find('.keyboard-num,.keyboard-num-footer').show();
      }
      // 纯数字
      if(this.option.type == 'money'){
        $(this.key).find('.keyboard-num,.keyboard-num-footer,.keyboard-letter,.keyboard-symbol').hide();
        $(this.key).find('.keyboard-money').show();
      }
      // 字母
      if(this.option.type == 'letter'){
        $(this.key).find('.keyboard-num,.keyboard-num-footer,.keyboard-money,.keyboard-symbol').hide();
        $(this.key).find('.keyboard-letter').show();
      }
      // 字符
      if(this.option.type == 'symbol'){
        $(this.key).find('.keyboard-num,.keyboard-num-footer,.keyboard-money,.keyboard-letter').hide();
        $(this.key).find('.keyboard-symbol').show();
      }
    },
    // 初始化样式配置
    initAttribute () {
      if(this.option.class){
        $(this.$refs.keyboard_box).addClass(this.option.class);
      }
      if(this.option.style){
        $(this.$refs.keyboard_box).css(this.option.style);
      }
    },
    clear () {
      this.val = '';
    }
  },
  computed: {},
  watch: {
    val: function(v){
      this.$nextTick(function(){
        this.$refs.keyboard_input.scrollLeft = 1000;
      })
      return v;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.kb-box{
  display: inline-block;
  width: 100%;
  height: .4rem;
  box-sizing: border-box;
  text-align: left;
  word-wrap: normal;
  padding: 0rem .4rem 0rem .05rem;
  border: 1px solid #eee;
  background: white;
}
.kb-tem{
  width: 100%;
  height: 100%;
  position: relative;
}
.kb-input{
  display: inline-block;
  max-width: 96%;
  padding: 0rem;
  margin: 0rem;
  box-sizing: border-box;
  overflow-x: scroll;
  vertical-align: middle;
}
.kb-flicker{
  display: inline-block;
  width: 2px;
  height: 50%;
  background: rgba(22, 36, 92, 1);
  vertical-align: middle;
  margin-left: -2px;
  animation: flicker 1s infinite;
}
.kb-clear{
  width: .4rem;
  height: 100%;
  position: absolute;
  top: 0rem;
  right: -.4rem;
  text-align: center;
}
.kb-clear:after{
  display: inline-block;
  content: '♂';
  vertical-align: middle;
  text-align: center;
}
@keyframes flicker{
  0%,100%{
    background: rgba(22, 36, 92, 1);
  }
  50%{
    background: rgba(22, 36, 92, 0);
  }
}
</style>



