import _ from 'underscore'

export default {
    // 数据储存
    data (key, obj) {
    	let json;
    	let backObj;
        if(typeof obj == 'object'){
            json = JSON.stringify(obj);
        }else{
            json = obj;
        }
        if(obj === undefined){
            backObj = localStorage.getItem(key);
        }else{
            localStorage.setItem(key, json);
        }
        switch(backObj){
            case 'null':
                backObj = null;
                break;
            case 'undefined':
                backObj = undefined;
                break;
            case 'true':
                backObj = true;
                break;
            case 'false':
                backObj = false;
                break;
            default:
                if(!isNaN(+backObj)){
                    backObj = +backObj;
                }
                break;
        }
        return backObj;
    },
}


