// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

// 影藏测试环境下警告
Vue.config.productionTip = false

let lastLog;
let self = this;
console.oldLog = console.log;
// 捕获console
if(false){
console.log = function(str) {
  let li = document.createElement('li');
  let main = document.getElementById('console_main');
  li.innerHTML = '<div class="console-erruri">'+str+'</div><div class="console-errmsg"></div>';
  console.oldLog(str);
  lastLog = str;
  if(main){
    main.prepend(li);
  }
}
}

let err;
err = Vue.config.errorHandler;
// 捕获异常和错误
Vue.config.errorHandler = function (...values) {
    let [type] = values;
    let li = document.createElement('li');
    let main = document.getElementById('console_main');
    li.innerHTML = '<div class="console-erruri">'+type.message+'</div><div class="console-errmsg">&gt;&gt;'+type.stack+'</div>';
    if(main){
        main.prepend(li);
    }
    if(err){
        err(values);
    }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



