<template>
  <div id="app">
    <div class="head">
      <div class="head-center">
        <div class="head-center-body">{{msg}}</div>
      </div>
      <div class="head-icon left" @click="leftBack">返回</div>
      <div class="head-icon right" @click="backHome"></div>
    </div>
    <router-view :set-head="setHead"/>
    <con v-if="showConsole"></con>
  </div>
</template>

<script>
import con from '@/components/ConsoleComponent'
import utils from '@/js/utils'
export default {
  name: 'App',
  data () {
    return {
      msg: 'Welcome to My App',  //标题
      showConsole: false,        //是否打开console
      left: '',                  //左按钮
      right: ''                  //右按钮
    }
  },
  mounted () {
    // this.showConsole = JSON.parse(localStorage.getItem('console')).active;
    this.showConsole = utils.data('console');
  },
  methods: {
    // 设置头部
    setHead (options = {}) {
      this.msg = options.msg;
      this.left = options.leftCb;
      this.right = options.rightCb;
    },
    // 左按钮功能
    leftBack () {
      this.left && this.left();
      this.left = '';
      if(this.$route.name == 'HelloWorld'){
        // darren.goHome();
        document.location = "js://webview?arg1=111&arg2=222";
      }else{
        this.$router.go(-1);
      }
    },
    // 右按钮功能
    backHome () {
      this.right && this.right();
      this.right = '';
      this.$router.push({path: '/'})
    }
  },
  components: { con }
}
</script>

<style>
html,body{
  width: 100%;
  height: 100%;
  background: #F8F2Dc;
}
#app {
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #16245C;
  font-size: .14rem;
  background: #F8F2Dc;
  overflow-y: scroll;
}
@media only screen and (min-width: 320px){
  html {
    font-size:625% !important;
  }
}
@media only screen and (min-width: 640px){
  html {
    font-size:1250% !important;
  }
}
@media only screen and (min-width: 750px){
  html {
    font-size:1500% !important;
  }
}
@media only screen and (min-width: 1242px){
  html {
    font-size:1875% !important;
  }
}
html,body,h1,h2,h3,h4,h5,h6,ul,li,ol,p{
  padding: 0;
  margin: 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
img{
  width: 100%;
}
.head{
  width: 100%;
  height: .44rem;
  background: linear-gradient(#0D1740, #16245C);
  box-shadow: .01rem 0rem .15rem white;
}
.head-center{
  width: 100%;
  height: 100%;
  line-height: .44rem;
  float: left;
  color: #F8F2DC;
}
.head-center-body{
  margin: 0 .44rem;
}
.head-icon{
  width: .44rem;
  height: 100%;
  float: left;
}
.left{
  margin-left: -100%;
  line-height: .44rem;
  text-align: center;
  color: #A39F93;
}
.right{
  margin-left: -.44rem;
  background-image: url(./assets/moshou.png);
  background-size: 100%;
  background-repeat: space;
}
.bottom-box{
  width: 100%;
  height: 3rem;
  overflow: hidden;
  position: fixed;
  bottom: 0;
}
/*文字上下居中*/
.clearfix:after { content:"."; display:block; height:0; clear:both; visibility:hidden; }
.clearfix { display:inline-block; }
.clearfix {display:block;}
.block:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  /*margin-right: -0.25rem;  Adjusts for spacing */
}

/*checkbox样式*/
.checkboxThree {
  width: .48rem;
  height: .16rem;
  background: #333;
  border-radius: .2rem;
  position: relative;
  display: inline-block;
}
.checkboxThree:before {
  content: 'On';
  position: absolute;
  top: .048rem;
  left: .052rem;
  height: .008rem;
  color: #26ca28;
  font-size: 0rem;
}
.checkboxThree:after {
  content: 'Off';
  position: absolute;
  top: .048rem;
  left: .336rem;
  height: .008rem;
  color: #ddd;
  font-size: 0rem;
}
.checkboxThree label {
  display: block;
  width: .208rem;
  height: .088rem;
  border-radius: .2rem;
 
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  -o-transition: all .5s ease;
  -ms-transition: all .5s ease;
  transition: all .5s ease;
  cursor: pointer;
  position: absolute;
  top: .036rem;
  z-index: 1;
  left: .048rem;
  background: #ddd;
}
.checkboxThree label.active{
  left: .24rem;
  background: #26ca28;
}
.checkboxThreeInput{
  visibility: hidden;
  margin: 0rem;
}

/*键盘公共样式*/
.keyboard-main{
  position: fixed;
  top: 0rem;
  left: 0rem;
  width: 100%;
  height: 100%;
  color: #000;
  font-size: .12rem;
  text-align: center;
  z-index: 10;
}
.keyboard-layer{
  position: absolute;
  bottom: 0rem;
  left: 0rem;
  width: 100%;
  height: 2rem;
  background: #dadada;
}
.keyboard-num{
  display: flex;
  box-sizing: border-box;
}
.keyboard-num-left{
  width: .5rem;
  height: 1.5rem;
  background: white;
  margin: .05rem;
  border-radius: .05rem;
}
.keyboard-num-left p{
  width: 100%;
  height: 25%;
}
.keyboard-num-content{
  flex: 1;
  height: 1.55rem;
}
.keyboard-num-content li{
  display: flex;
  width: 100%;
  height: 33.33%;
}
.keyboard-num-content li p{
  flex: 1;
  background: white;
  border-radius: .05rem;
  margin: .05rem .05rem 0rem 0rem;
}
.keyboard-num-left p:before,.keyboard-num-content li p:before,.keyboard-num-footer p:before,.keyboard-money p:before,.keyboard-letter p:before,.keyboard-symbol p:before{
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.keyboard-num-left p span,.keyboard-num-content p span,.keyboard-num-footer p span,.keyboard-money p span,.keyboard-letter p span,.keyboard-symbol p span{
  vertical-align: middle;
}
.keyboard-num-footer{
  width: 100%;
  height: .4rem;
  display: flex;
}
.keyboard-num-footer p{
  flex: 1;
  background: white;
  margin: 0rem 0rem .05rem .05rem;
  border-radius: .05rem;
}
.keyboard-num-footer p:last-child{
  margin-right: .05rem;
}
.keyboard-num-footer  .keyboard-num-zero{
  width: .9rem;
  flex: auto;
}

.keyboard-num-space{
  display: inline-block;
  width: .2rem;
  height: .1rem;
  border-top: none;
  border-bottom: 1px solid #000;
  border-left: 1px solid #000;
  border-right: 1px solid #000;
  vertical-align: middle;
}
.keyboard-money,.keyboard-letter,.keyboard-symbol{
  width: 100%;
  height: 100%;
  padding: .05rem;
  box-sizing: border-box;
}
.keyboard-money li,.keyboard-letter li,.keyboard-symbol li{
  display: flex;
  height: 25%;
}
.keyboard-money li p,.keyboard-letter li p,.keyboard-symbol li p{
  flex: 1;
  background: white;
  border-radius: .05rem;
  margin: .05rem .05rem 0rem 0rem;
}
.keyboard-money li p:last-child{
  margin-right: 0rem;
}
.keyboard-money li:first-child p{
  margin-top: 0rem;
}
.keyboard-money li p.gray,.keyboard-num-footer p.gray,.keyboard-num-content li p.gray,.keyboard-letter li p.gray,.keyboard-symbol li p.gray{
  background: #ccc;
}
.keyboard-money li p.touch,.keyboard-num-footer p.touch,.keyboard-num-content li p.touch,.keyboard-num-left p.touch,.keyboard-letter li p.touch,.keyboard-symbol li p.touch{
  background: #a0a0a0;
}
.keyboard-main .keyboard-lower{
  display: flex;
}
.keyboard-main.active .keyboard-lower{
  display: none;
}
.keyboard-main .keyboard-capital{
  display: none;
}
.keyboard-main.active.active .keyboard-capital{
  display: flex;
}
.ml-10{
  margin-left: .1rem;
}
</style>



