<template>
    <div>
        <header class="mine-head">
            <button class="mine-search" @click="refresh">刷新</button>
            <div class="mine-time">
                <span>{{huors | zero}}</span>:
                <span>{{minutes | zero}}</span>:
                <span>{{seconds | zero}}</span>
            </div>
            <button class="mine-show" @click="showMine">完毕</button>
        </header>
        <ul>
            <li class="item-parent" v-for="items in list">
                <div class="item-son" :class="{active: o.mark || o.flag}" v-for="(o,i) in items" @touchstart="touchstart($event,o.id)" @touchmove.stop.prevent="touchmove" @touchend="touchend($event, o.id)">
                    <div :class="{num: (o.type==2 || !o.mark) || (o.type == 0), mine: o.type==1 && o.mark}">
                        <span v-show="(o.type == 2 && o.mark) || (o.mark && o.type==0)" class="num-after">{{o.num}}</span>
                        <span v-show="!o.mark && ~markList.indexOf(o.id)" class="num-after">标记</span>
                        <p class="after-tip" :class="{'after-tip-right': mineNum == i+1}" v-show="markId==o.id">
                            <span class="after-mark" @touchstart.prevent="markMine(o.id)">标记</span>
                            <span class="after-mark" @touchstart.prevent="clearMark(o.id)">清除</span>
                        </p>
                    </div>
                </div>
            </li>
        </ul>
        <div class="mine-screen" :class="{open: open}" v-show="showScreen">
            <div class="mine-screen-wall">
                <div class="mine-screen-left">
                    <div class="mine-surface1"></div>
                    <div class="mine-surface2"></div>
                    <div class="mine-surface3"></div>
                    <div class="mine-surface4"></div>
                    <div class="mine-surface5"></div>
                    <div class="mine-surface6"></div>
                </div>
                <div class="mine-screen-right">
                    <div class="mine-surface1"></div>
                    <div class="mine-surface2"></div>
                    <div class="mine-surface3"></div>
                    <div class="mine-surface4"></div>
                    <div class="mine-surface5"></div>
                    <div class="mine-surface6"></div>
                </div>
            </div>
            <div class="mine-screen-content" @click="start">开始</div>
        </div>
        <div class="mine-end" :class="{'end-mark':endMark}">{{endTxt}}</div>
    </div>
</template>
<script>
import _ from 'underscore'
export default {
    name: 'SweepMineComponent',
    props: ['setHead'],
    data () {
        return {
            status: false,    //游戏状态
            boxNum: 0,        //格子数
            mineNum: 10,      //雷数
            /** 
             * 扫雷二维数组
             * @id:二维下标
             * @type:0-空白 1-雷 2-数字
             * @num:type为2的情况下显示的周围雷数
             * @mark:表示是否点击过
             * @flag:点击后的凹陷动画
             */
            list: [],
            endMark: false,
            touchTime: 0,     //长按时间
            markId: '',       //选中元素
            timeKey: 0,       //提示key
            maxBut: '30',     //默认按钮大小
            markList: [],     //标记列表,
            mineArr: [],      //雷数组,以格子数为下标,需要转换成二维下标
            open: false,      //开门动画
            showScreen: true, //显示门
            huors: 0,         //时
            minutes: 0,       //分
            seconds: 0,       //秒
            timeId: 0,        //定时器id
            time: 0,          //开始游戏的时间
            endTxt: '',       //结束游戏文案
            row: 0,           //行
            col: 0            //列
        }
    },
    mounted () {
        // this.horizontal();
        this.setHead({
            msg: '扫雷',
            leftCb: ()=>{
                clearTimeout(this.timeId);
            },
            rightCb: ()=>{
                clearTimeout(this.timeId);
            }
        });
        setTimeout( ()=>{
            this.open = true;
        },0)

        document.addEventListener('visibilitychange', (e)=>{
            if(this.showScreen || this.status){return;}
            if(document.hidden){
                clearTimeout(this.timeId);
            }else{
                // 当前时间-离开页面时候计时器时间=离开的时间
                clearTimeout(this.timeId);
                this.setTime();
            }
        })

        setTimeout( () => {
            this.boxNum = 100;
            this.searchRow();
            this.init();
        }, 1200)
    },
    methods: {
        init () {
            // 二维数组宽高长度
            let sqrt = Math.sqrt(this.boxNum);
            let w = window.innerWidth;
            // 扫雷二维数组
            let arr = [];
            if(this.boxNum <= 0 || this.boxNum < this.mineNum){return;}
            // 初始化雷
            let mineArr = this.initMine();
            // for (let i = 0; i < sqrt; i++) {
            for (let i = 0; i < this.row; i++) {
                // 行
                let row = [];
                // for (let j = 0; j < sqrt; j++){
                for (let j = 0; j < this.col; j++){
                    //没有雷 空白type:0
                    if(!~Object.keys(mineArr).indexOf(i+','+j)){
                        row.push({id: i+','+j,type: 0, mark: false, flag: false});
                    }else{
                        let mark = mineArr[i+','+j];
                        //雷 type:1
                        if(mark == -1){
                            row.push({id: i+','+j,type: 1, mark: false, flag: false});
                        }else{
                            row.push({id: i+','+j,type: 2,num: mark, mark: false, flag: false});
                        }
                    }
                }
                arr.push(row);
            };
            this.list = arr;
            // this.huors = this.minutes = this.seconds = 0;
            clearTimeout(this.timeId);
            this.time = new Date();
            if(!this.showScreen){
                this.setTime(0);
            }
        },
        // 初始化雷
        initMine () {
            // 雷数组
            let arr = [];
            // 标记
            let mark = [];
            let sqrt = Math.sqrt(this.boxNum);
            while(mark.length < this.mineNum){
                // 从100中抽
                let num = ~~(Math.random()*this.boxNum);
                // 防重
                if(!~mark.indexOf(num)){
                    // 选中标记
                    mark.push(num);
                    // 行
                    // let first = ~~(num/sqrt);
                    let first = ~~(num/this.row);
                    // 列
                    // let last = ~~(num%sqrt);
                    let last = ~~(num%this.col);
                    // 雷
                    arr[first+','+last] = -1;
                    // 雷周围标记数
                    arr = this.countMine(first+1, last-1, arr);  //左下
                    arr = this.countMine(first+1 , last, arr);   //下
                    arr = this.countMine(first+1, last+1, arr);  //右下
                    arr = this.countMine(first, last-1, arr);    //左
                    arr = this.countMine(first, last+1, arr);    //右
                    arr = this.countMine(first-1, last, arr);    //上
                    arr = this.countMine(first-1, last-1, arr);  //左上
                    arr = this.countMine(first-1, last+1, arr);  //右上
                }
            }
            this.mineArr = mark;
            return arr;
        },
        // 标记雷周围
        countMine (first, last, arr) {
            // 边界判断
            if(this.borderCheck(first, last)){
                return arr;
            }
            let key = first + ',' + last;
            // 雷跳过
            if(arr[key] == -1){
                return arr;
            }if(!~Object.keys(arr).indexOf(key)){
                arr[key] = 1;
            }else{
                arr[key] = arr[key]+1;
            }
            return arr;
        },
        // 触摸
        touchstart (e, key) {
            e.preventDefault();
            // 判断长按时间
            this.touchTime = new Date();
            let self = this;
            let first = key.split(',')[0];
            let last = key.split(',')[1];
            let target = e.target;
            if(this.status){return;}
            if(target.className == 'after-mark'){
                return;
            }
            this.list[first][last].flag = true;
            if(!~this.markList.indexOf(key+'')){
                this.markId = '';
            }
            // 按钮下陷样式
            if(!this.list[first][last].mark){
                this.timeKey = setTimeout(function(){
                    self.markId = key;
                }, 500)
            }
        },
        // 触摸滑动
        touchmove () {
        },
        // 触摸离开
        touchend (e, key) {
            let first = key.split(',')[0];
            let last = key.split(',')[1];
            let nowObj = this.list[first][last];
            clearTimeout(this.timeKey);
            let target = e.target;
            if(this.status){return;}
            if(target.className == 'after-mark' || ~this.markList.indexOf(key+'')){
                nowObj.flag = false;
                return;
            }
            // 长按返回
            if(new Date() - this.touchTime > 500){
                if(!nowObj.mark){
                    this.markId = key;
                }
                nowObj.flag = false;
            }else{
                if(nowObj.type == 2){
                    nowObj.mark = true;
                }
                if(nowObj.type == 1){
                    nowObj.mark = true;
                    this.status = true;
                    this.endTxt = '游戏完结';
                    clearTimeout(this.timeId);
                    setTimeout(()=>{
                        this.endMark = true;
                    },0)
                    return;
                }
                if(nowObj.type == 0 && !nowObj.mark){
                    this.checkEmpty(first, last);
                }
                // 将二维转换为一维,然后检查是否标记所有雷
                let list = _.flatten(this.list);
                let endArr = _.filter(list, function(o){
                    return o.type == 2 && !o.mark;
                })
                if(endArr.length <= 0){
                    this.status = true;
                    this.endTxt = '游戏胜利';
                    clearTimeout(this.timeId);
                    setTimeout(()=>{
                        this.endMark = true;
                    },0)
                    return;
                }
            }
        },
        // 显示所有的雷
        showMine () {
            for (var i = this.mineArr.length - 1; i >= 0; i--) {
                let num = this.mineArr[i];
                // 行
                let first = ~~(num/this.row);
                // 列
                let last = ~~(num%this.col);
                this.list[first][last].mark = true;
            };
        },
        // 点击空白
        checkEmpty (first, last) {
            // 拷贝原对象
            let list = this.list;
            // 待处理元素数组
            let wait = [];
            // 当前对象
            let nowObj = list[first][last];
            while(true){
                // 已点击
                if(!nowObj.mark){
                    nowObj.mark = true;
                }
                // 行
                first = +(nowObj.id.split(',')[0]);
                // 列
                last = +(nowObj.id.split(',')[1]);

                switch(nowObj.status){
                    case -1: 
                        break;
                    // 左上
                    case 2: 
                        first -= 1;
                        last -= 1;
                        nowObj.status = 3;
                        break;
                    // 上
                    case 3: 
                        first -= 1;
                        nowObj.status = 4;
                        break;
                    // 右上
                    case 4: 
                        first -= 1;
                        last += 1;
                        nowObj.status = 5;
                        break;
                    // 左
                    case 5: 
                        last -= 1;
                        nowObj.status = 6;
                        break;
                    // 右
                    case 6: 
                        last += 1;
                        nowObj.status = 7;
                        break;
                    // 左下
                    case 7: 
                        first += 1;
                        last -= 1;
                        nowObj.status = 8;
                        break;
                    // 下
                    case 8: 
                        first += 1;
                        nowObj.status = 9;
                        break;
                    // 右下
                    case 9: 
                        first += 1;
                        last += 1;
                        nowObj.status = 10;
                        break;
                    case 10: 
                        nowObj.status = -1;
                        break;
                    // 默认左
                    default: 
                        last -= 1;
                        nowObj.status = 2;
                        break;
                }

                // 判断结束状态
                if(nowObj.status == -1 && wait.length <= 0){
                   return;
                }
                // 下一条判断边界
                if(this.borderCheck(first,last)){
                    continue;
                }
                if(nowObj.status == -1){
                    nowObj = wait.pop();
                    continue;
                }

                let obj = nowObj;
                // 下个状态对象
                nowObj = list[first][last];

                if(nowObj.mark){
                    nowObj = obj;
                    continue;
                }
                // 下个类型
                if(nowObj.type == 1 || nowObj.type == 2){
                    nowObj.mark = true;
                    nowObj = obj;
                    continue;
                }
                nowObj.mark = true;
                wait.push(nowObj);
            }
        },
        // 边界判断 true表示超过边界
        borderCheck (first, last) {
            let sqrt = Math.sqrt(this.boxNum);
            // if(first < 0 || first >= sqrt || last < 0 || last >= sqrt){
            if(first < 0 || first >= this.row || last < 0 || last >= this.col){
                return true;
            }
            return false;
        },
        // 标记雷
        markMine (key) {
            if(!~this.markList.indexOf(key)){
                this.markList.push(key);
            }
            this.markId = '';
        },
        // 解除标记
        clearMark (key) {
            if(~this.markList.indexOf(key)){
                this.markList.splice(this.markList.indexOf(key),1);
            }
            this.markId = '';
        },
        // 设置横屏
        horizontal () {
            let width = document.documentElement.clientWidth,
                height = document.documentElement.clientHeight,
                wrapper = document.getElementById('app'),
                style = '';
            if(width >= height){
                style += 'width:100%;';
                style += 'height:100%;';
                style += '-webkit-transform: rotate(0); transform: rotate(0);';
                style += '-webkit-transform-origin: 0 0;';
                style += 'transform-origin: 0 0;';
            }else{
                style += 'width:'+height+'px;';
                style += 'height:'+width+'px;';
                style += '-webkit-transform: rotate(90deg); transform: rotate(90deg);';
                style += '-webkit-transform-origin: '+width/2+'px '+ width/2 +'px;';
                style += 'transform-origin: '+width/2+'px '+width/2+'px;';
            }
            wrapper.style.cssText = style;
        },
        // 查询行列最优比
        searchRow () {
            let w = window.innerWidth;
            let h = window.innerHeight;
            let s = Math.sqrt(this.boxNum);
            let col = 0,row = 0;
            let maxCol = Math.floor(w/this.maxBut);
            let maxRow = Math.floor((h-84)/this.maxBut);
            if(s > maxCol || s > maxRow){
                col = maxCol;
                row = Math.ceil(this.boxNum/col);
            }else{
                col = s;
                row = s;
            }
            row = row > maxRow ? maxRow : row;
            this.row = row;
            this.col = col;
            this.boxNum = parseInt(row*col);
        },
        // 刷新游戏
        refresh () {
            this.status = false;
            this.endMark = false;
            this.huors = this.minutes = this.seconds = 0;
            clearTimeout(this.timeId);
            this.time = new Date();
            this.init();
        },
        // 开始游戏
        start () {
            this.open = false;
            setTimeout( () => {
                this.boxNum = 100;
                clearTimeout(this.timeId);
                this.time = new Date();
                this.setTime(0)
                this.showScreen = false;
                // this.searchRow();
                // this.init();
            }, 1000)
        },
        // 计时
        setTime () {
            this.timeId = setTimeout( ()=>{
                clearTimeout(this.timeId);
                let t = new Date() - this.time;
                let h = 60*60*1000;
                let m = 60*1000;
                let s = 1000;
                if(t - ((this.huors*h) + (this.minutes*m) + (this.seconds*s)) >= s){
                    this.huors = ~~(t / h);
                    this.minutes = ~~((t % h) / m);
                    this.seconds = ~~((t % m) / s);
                }
                if(this.huors>=99){
                    this.seconds = 0;
                    this.minutes = 0;
                    this.huors = 99;
                    return;
                }
                this.setTime();
            }, 100)
        }
    },
    filters: {
        zero (num) {
            if(num < 10){
                return '0'+num;
            }else{
                return num;
            }
        }
    }
}
</script>
