<template>
  <ul class="btn-list">
    <li @click="to('games')">游戏</li>
    <li @click="to('css')">样式库</li>
    <li @click="to('script')">js库</li>
    <li @click="to('canvas')">Canvas</li>
    <li @click="to('settings')">设置</li>
  </ul>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {}
  },
  mounted () {
    this.$parent.msg = '首页';
  },
  methods: {
    to (type) {
      this.$router.push({path: '/' + type})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.btn-list{
  margin: .3rem .4rem 0rem;
}
.btn-list li{
  margin: .3rem;
  font-size: .24rem;
  background: #E9CFAE;
}
.flex{
  display: flex;
  font-size: .12rem;
}
</style>



