<template>
  <div class="console" :class="{visibility: !showConsole}">
    <ul class="console-header" v-show="showConsole">
      <li class="block active">Console</li>
      <li class="block">Network</li>
    </ul>
    <div class="console-main" v-show="showConsole">
      <ul id="console_main"></ul>
    </div>
    <div class="console-footer" v-show="showConsole">
      <p class="block">&gt;</p>
      <input type="text" ref="console_crarr">
      <p class="block" style="width:.4rem;" @click="oncrarr">&crarr;</p>
    </div>
    <div class="console-box" :style="{top: top, left: left}" @touchstart.stop.prevent="start" @touchmove.stop.prevent="move($event)" @touchend.stop.prevent="end">
      <div class="blueball1 block" ref="box_t"><span v-show="showTxt" @touchstart="showConsole=false">隐藏</span></div>
      <div class="blueball2 block" ref="box_b" v-show="clearb"><span v-show="showTxt" @touchstart="showConsole=true">显示</span></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'con',
  data () {
    return {
      // 隐藏输出区域
      showConsole: false,
      // 用于隐藏显示按钮
      clearb: false,
      // 展示双球
      active: false,
      // true表示动画中
      animation: false,
      // 需要在动画前隐藏文案
      showTxt: false,
      // setTimeout的key
      clearT: 0,
      // 点击时间判断是否移动
      clickTime: 0,
      touches: {
        pageX: 0,
        pageY: 0
      },
      top: 0,
      left: 0
    }
  },
  methods: {
    // 输出回车
    oncrarr () {
      // 输入代码
      let str = this.$refs.console_crarr.value;
      // 输出代码的列
      let li = document.createElement('li');
      // 显示区域
      let main = document.getElementById('console_main');
      this.$refs.console_crarr.value = '';
      // 执行代码
      let f = eval(str);
      li.innerHTML = '<div class="console-erruri">'+str+'</div><div class="console-errmsg">&gt;&gt;'+ eval(str) +'</div>';
      if(main){
        main.prepend(li);
      }
    },
    start () {
      this.clickTime = new Date();
    },
    move (e) {
      let o = e.currentTarget;
      let {touches} = e;
      // 分裂时不能拖动
      if(this.active){return;}
      // 第一次进入
      if(!this.touches.pageX || !this.touches.pageY){
        this.touches = touches[0];
        return;
      }
      if(new Date() - this.clickTime > 200){
        let top = parseFloat(this.top) - (this.touches.pageY - touches[0].pageY);
        let left = parseFloat(this.left) - (this.touches.pageX - touches[0].pageX);
        this.top = top + 'px';
        this.left = left + 'px';
        this.touches = touches[0];
      }
    },
    end () {
      this.touches = {pageX: 0,pageY: 0};
      if(new Date() - this.clickTime > 200){return;}
      if(this.animation){ return;}
      clearTimeout(this.clearT);
      if(this.active){
        this.animation = true;
        this.showTxt = false;
        this.clearT = setTimeout(() => {
          this.active = false;
          this.clearb = false;
          this.$refs.box_t.className = 'blueball1 block';
          this.$refs.box_b.className = 'blueball2 block';
          this.animation = false;
        }, 800)
        this.$refs.box_t.className = 'blueball1 block animation3';
        this.$refs.box_b.className = 'blueball2 block animation4';
      }else{
        this.active = true;
        this.clearb = true;
        this.animation = true;
        this.clearT = setTimeout(() => {
          this.animation = false;
          this.showTxt = true;
        }, 800);
        this.$refs.box_t.className = 'blueball1 block animation1';
        this.$refs.box_b.className = 'blueball2 block animation2';
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.console{
  position: fixed;
  bottom: 0rem;
  left: 0rem;
  width: 100%;
  height: 3rem;
  font-size: .14rem;
  background: #E9CFAE;
}
.console-header{
  display: flex;
  width: 100%;
  height: 10%;
  border-bottom: 1px solid #16245C;
}
.console-header li{
  margin: .02rem .2rem 0rem .2rem;
  padding-bottom: .02rem;
  flex: 1;
  text-align: center;
}
.console-header li.active{
  border-bottom: .02rem solid #A39F93;
}
.console-main{
  width: 100%;
  height: 80%;
  overflow-y: scroll;
  background: #F8F2DC;
  box-sizing: border-box;
  border: .05rem solid #E9CFAE;
  font-size: .12rem;
}
.console-footer{
  width: 100%;
  height: 10%;
  display: flex;
  background: white;
  color: #A39F93;
}
.console-footer p{
  width: .2rem;
  text-align: center;
}
.console-footer input{
  padding: 0;
  margin: 0;
  border: none;
  outline:none;
  flex: 1;
}
.console-main li{
  border-bottom: 1px solid #E9CFAE;
  text-align: left;
}
.console-erruri{
  width: 100%;
}
.console-box{
  position: absolute;
  top: 0px;
  left: 0px;
  width: .3rem;
  height: .3rem;
  background: rgb(13, 23, 64, .8);
  border-radius: .3rem;
  font-size: .12rem;
  color: #F8F2DC;
}
.blueball1{
  width:.3rem;
  height:.3rem;
  background: #000;
  border-radius:50%;
  position:relative;
  top: 0;
  left:0;
  z-index:2;
}
.blueball2{
  width:.3rem;
  height:.3rem;
  background: #000;
  border-radius:50%;
  position:relative;
  top:0rem;
  left:0rem;
  z-index:2;
}
.animation1{
  animation:bball1 1s infinite;
  animation-iteration-count: 1;
}
.animation2{
  animation:bball2 1s infinite;
  animation-iteration-count: 1;
}
.animation3{
  animation:bball3 1s infinite;
  animation-iteration-count: 1;
}
.animation4{
  animation:bball4 1s infinite;
  animation-iteration-count: 1;
}
.visibility{
  background: rgb(0,0,0,0);
}

@keyframes bball1{
  0%{
    height:.3rem;
  }
  80%{
    height:.36rem;
  }
  100%{
    height:.3rem;
  }
}
@keyframes bball3{
  0%{
    height:.3rem;
  }
  20%{
    height:.36rem;
  }
  100%{
    height:.3rem;
  }
}
@keyframes bball4{
  0%{
    top: 0rem;
    height:.3rem;
  }
  20%{
    top: -.16rem;
    height:.36rem;
  }
  100%{
    top: -.3rem;
    height:.3rem;
  }
}
@keyframes bball2{
  0%{
    top: -.3rem;
    height:.3rem;
  }
  80%{
    top: -.16rem;
    height:.36rem;
  }
  100%{
    top: 0rem;
    height:.3rem;
  }
}
</style>



