<template>
    <div class="body">
      <canvas ref="line"></canvas>
    </div>
</template>

<script>
export default {
  name: 'LightLineComponent',
  data () {
    return {
      x: 0,
      y: 0,
      p: 0,
      n: 0,
      r: 0,
      dx: 0,
      dy: 0,
      ctx: '',
      width: 980,
      color: 0,
      point: 0,
      height: 1743,
      canvas: '',
      trashold: 10,
      particles: [],
      deltaangle: 0,
      noofpoints: 200,
      innerpoints: [],
      outerpoints: []
    }
  },
  mounted () {
    // 获取canvas对象
    this.canvas = this.$refs.line
    // 获取画布
    this.ctx = this.canvas.getContext('2d')
    // 爱心线条的个数
    this.deltaangle = Math.PI * 2 / this.noofpoints
    this.r = Math.min(this.height, this.width) * 0.5
    this.resize()
    this.ctx.globalAlpha = 0.3
    this.ctx.globalCompositeOperation = 'source-over'

    // this.render()
    setInterval(this.render, 30)
  },
  methods: {
    distance (x1, y1, x2, y2) {
      return Math.sqrt(Math.pow(y2 - y1, 2) + Math.pow(x2 - x1, 2))
    },
    resize () {
      let ctx = this.ctx
      let canvas = this.canvas
      this.height = ctx.canvas.clientHeight
      this.width = ctx.canvas.clientWidth
      if (ctx.canvas.clientWidth !== canvas.width || ctx.canvas.clientHeight !== canvas.height) {
        canvas.width = this.width
        canvas.height = this.height
        // 图像位置
        ctx.translate(canvas.width / 2, canvas.height / 2)
        ctx.rotate(-Math.PI)
        this.innerpoints = []
        this.r = 1
        // let sdf = 0
        for (let i = this.deltaangle; i <= Math.PI * 2; i += this.deltaangle) {
          this.x = this.r * 16 * Math.pow(Math.sin(i), 3)
          this.y = this.r * (13 * Math.cos(i) - 5 * Math.cos(2 * i) - 2 * Math.cos(3 * i) - Math.cos(4 * i))
          this.innerpoints.push({
            x: this.x,
            y: this.y
          })

          this.x = 10 * this.r * 16 * Math.pow(Math.sin(i), 3)
          this.y = 10 * this.r * (13 * Math.cos(i) - 5 * Math.cos(2 * i) - 2 * Math.cos(3 * i) - Math.cos(4 * i))
          this.outerpoints.push({
            x: this.x,
            y: this.y
          })
          // 随机线条速度
          let step = this.random(0.001, 0.003, true)
          this.particles.push({
            step: step,
            x: this.x,
            y: this.y
          })
        }
      }
    },
    random (min, max, isFloat) {
      if (isFloat) {
        return Math.random() * (max - min) + min
      }
      return ~~(Math.random() * (max - min) + min)
    },
    draw () {
      let ctx = this.ctx
      ctx.fillStyle = 'rgba(0,0,0,0.03)'
      ctx.fillRect(-this.width, -this.height, this.width * 2, this.height * 2)
      ctx.beginPath()

      for (let i = 0; i < this.innerpoints.length; i++) {
        let s = this.outerpoints[i]
        let d = this.innerpoints[i]
        this.point = this.particles[i]

        if (this.distance(this.point.x, this.point.y, d.x, d.y) > 10) {
          this.dx = d.x - s.x
          this.dy = d.y - s.y
          this.point.x += this.dx * this.point.step
          this.point.y += this.dy * this.point.step
          this.color = this.distance(0, 0, this.point.x, this.point.y)
          ctx.beginPath()
          ctx.fillStyle = 'hsl(' + (this.color % 360) + ',100%,50%)'
          ctx.arc(this.point.x, this.point.y, 2, 0, Math.PI * 2, false)
          ctx.closePath()
          ctx.fill()
        } else {
          this.point.x = d.x
          this.point.y = d.y
          ctx.beginPath()
          ctx.arc(this.point.x, this.point.y, 2, 0, Math.PI * 2, false)
          ctx.closePath()
          ctx.fill()
          this.particles[i].x = s.x
          this.particles[i].y = s.y
          this.particles[i].step = this.random(0.001, 0.003, true)
        }
      }
    },
    render () {
      this.resize()
      this.draw()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .body{
    width: 100%;
    height: 100%;
  }
  canvas{
    width: 100%;
    height: 100%;
  }
</style>



