import Vue from 'vue'
import $ from 'n-zepto'
import Router from 'vue-router'
import vueResource from 'vue-resource'
import HelloWorld from '@/components/HelloWorld'
import SvgDeformation from '@/components/SvgDeformation'
import LayerComponent from '@/components/LayerComponent'
import InputComponent from '@/components/InputComponent'
import MiddleComponent from '@/components/MiddleComponent'
import CanvasComponent from '@/components/CanvasComponent'
import SettingsComponent from '@/components/SettingsComponent'
import LightLineComponent from '@/components/LightLineComponent'
import SweepMineComponent from '@/components/SweepMineComponent'
import ConnectionComponent from '@/components/ConnectionComponent'
import OpticalLineComponent from '@/components/OpticalLineComponent'
import CanvasTransformComponent from '@/components/CanvasTransformComponent'

Vue.use(vueResource)
Vue.use(Router)

let route = new Router({
  mode: 'hash',
  routes: [
    // 首页
    {
      path: '',
      name: 'HelloWorld',
      component: HelloWorld
    },
    // 设置相关
    {
      path: '/settings',
      name: 'SettingsComponent',
      component: SettingsComponent
    },
    // 中间页
    {
      path: '/:id',
      name: 'MiddleComponent',
      component: MiddleComponent
    },
    // 扫雷游戏
    {
      path: '/games/mineSweep',
      name: 'SweepMineComponent',
      component: SweepMineComponent
    },
    // 连连看
    {
      path: '/games/connection',
      name: 'ConnectionComponent',
      component: ConnectionComponent
    },
    // 弹框
    {
      path: '/css/layer',
      name: 'LayerComponent',
      component: LayerComponent
    },
    // svg纯css形状变换动画
    {
      path: '/css/svgDeformation',
      name: 'SvgDeformation',
      component: SvgDeformation
    },
    // 光线流动动画
    {
      path: '/css/lightLine',
      name: 'LightLineComponent',
      component: LightLineComponent
    },
    // 模拟输入框
    {
      path: '/jsdemo/input',
      name: 'InputComponent',
      component: InputComponent
    },
    // canvas Api
    {
      path: '/canvas/canvasApi',
      name: 'CanvasComponent',
      component: CanvasComponent
    },
    // canvas 变换
    {
      path: '/canvas/canvasTransform',
      name: 'CanvasTransformComponent',
      component: CanvasTransformComponent
    },
    {
      path: '/canvas/opticalLine',
      name: 'OpticalLineComponent',
      component: OpticalLineComponent
    }
  ]
})
route.beforeEach((to, from, next) => {
  $('.keyboard-main').remove();
  document.body.style = '';
  return next();
})


export default route


