<template>
  <ul class="btn-list" v-if="list">
      <li v-for="l in list" @click="go(l.path)" v-bind:key="l.name">{{l.name}}</li>
  </ul>
</template>

<script>
import dataJson from '@/js/appData'
export default {
  name: 'MiddleComponent',
  data () {
    return {
      list: []
    }
  },
  mounted () {
    let id = this.$route.params.id;
    let str;
    if(dataJson[id]){
      switch(id){
        case 'css':
          str = '样式';
          break;
        case 'games':
          str = '游戏';
          break;
        case 'script':
          str = 'js插件';
          break;
        case 'canvas':
          str = 'canvas';
          break;
        default:
          str = '404';
          break;
      }
      this.$parent.msg = str;
      this.list = dataJson[id];
    }else{
      this.$parent.msg = '404';
    }
    // this.$http.get('api/games')
    //   .then((data) => {
    //     this.list = data.body.data
    //   })
  },
  methods: {
    go (path) {
      this.$router.push({path: path})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.btn-list{
  margin: .3rem .4rem 0rem;
}
.btn-list li{
  margin: .3rem;
  font-size: .24rem;
  background: #E9CFAE;
}
</style>



