<template>
  <ul class="set-list">
    <li class="clearfix">
      <span style="float:left;">console</span>
      <div class="checkboxThree" @click.prevent="checkConsole">
        <input type="checkbox" value="1" id="checkboxThreeInput" name="" class="checkboxThreeInput"/>
        <label for="checkboxThreeInput" :class="{active: active}"></label>
      </div>
    </li>
    <li class="clearfix" style="text-align:left;">
      <span>样式配色</span>
      <div class="flex">
        <p style="background:#0D1740;">0D1740</p>
        <p style="background:#F8F2DC">F8F2DC</p>
        <p style="background:#A39F93">A39F93</p>
        <p style="background:#E9CFAE">E9CFAE</p>
        <p style="background:#16245C">16245C</p>
      </div>
    </li>
  </ul>
</template>

<script>
import utils from '@/js/utils'
export default {
  name: 'SettingsComponent',
  data () {
    return {
      active: false
    }
  },
  mounted () {
    this.$parent.msg = '设置';
    // this.active = JSON.parse(localStorage.getItem('console')).active;
    this.active = utils.data('console');
  },
  methods: {
    to (type) {
      this.$router.push({path: '/' + type})
    },
    checkConsole () {
      this.active = !this.active;
      this.$parent.showConsole = this.active;
      // localStorage.setItem('console', JSON.stringify({active: this.active}));
      utils.data('console', this.active);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.set-list{
  position: relative;
}
.set-list li{
  font-size: .16rem;
  background: #F8F2DC;
  text-align: right;
  padding: .1rem .2rem;
  border-bottom: 1px solid #E9CFAE;
}
.flex{
  display: flex;
  font-size: .12rem;
}
.right-back{
  width: .1rem;
  height: .1rem;
  background: #F8F2DC;
  display: inline-block;
  border-radius: .04rem;
  border-right: .02rem solid #E9CFAE;
  border-top: .02rem solid #E9CFAE;
  transform: rotate(45deg);
  transform-origin: center;
  vertical-align: middle;
}
</style>



