<template>
    <div>
        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="width:100%;height:4rem;">
          <path id="animate1" />
          <path id="animate2" />
          <path id="animate3" />
          <path id="animate4" />
        </svg>
    </div>
</template>

<script>
export default {
  name: 'SvgDeformation',
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  /* 15% 40% 65% 90%都是重复帧，为了让图形变形后做长时间停留 */
  /* 这里需要注意所有的路径必须是C，并且锚点数必须对应上 */
  @keyframes deform{
    0% {
      d:path('M329.233,182C325.167,268,268.5,323.333,187.5,323.732   C109.223,323.732,45.834,264,45.768,182c0-78.277,63.455-141.732,141.732-141.732C270.5,40.667,329.233,103.723,329.233,182z');
    }
    25% {
      d:path('M329.233,182C325.167,268,268.5,323.333,187.5,323.732   C109.223,323.732,45.834,264,45.768,182c0-78.277,63.455-141.732,141.732-141.732C270.5,40.667,329.233,103.723,329.233,182z');
    }
    45% {
      d:path('M357.579,287.5c-13.579,0-190.579,0-165.079,0   c16,0-146.5,0.5-175.079,0C35,259.5,177,32.5,192.5,9C199.315,20.499,346,268,357.579,287.5z');
    }
    55% {
      d:path('M357.579,287.5c-13.579,0-190.579,0-165.079,0   c16,0-146.5,0.5-175.079,0C35,259.5,177,32.5,192.5,9C199.315,20.499,346,268,357.579,287.5z');
    }
    75% {
      d:path('M316.5,311c-19.5,0-223,0-258,0c0.5-20,0.5-227,0-258   c23.5,0,228,0,258,0C316.5,80.5,317,288,316.5,311z');
    }
    85% {
      d:path('M316.5,311c-19.5,0-223,0-258,0c0.5-20,0.5-227,0-258   c23.5,0,228,0,258,0C316.5,80.5,317,288,316.5,311z');
    }
    950% {
      d:path('M316.5,311c-19.5,0-223,0-258,0c0.5-20,0.5-227,0-258   c23.5,0,228,0,258,0C316.5,80.5,317,288,316.5,311z');
    }
    100% {
      d:path('M329.233,182C325.167,268,268.5,323.333,187.5,323.732   C109.223,323.732,45.834,264,45.768,182c0-78.277,63.455-141.732,141.732-141.732C270.5,40.667,329.233,103.723,329.233,182z');
    }
  }
  /* 这里描边动画实现时使用的周长为最长的圆形的路径长度，然后以1/4周长延长 */
  @keyframes animate1 {
    0%{stroke-dashoffset:0}
    100%{stroke-dashoffset:942.48}
  }
  @keyframes animate2 {
    0%{stroke-dashoffset:235.62}
    100%{stroke-dashoffset:1178.1}
  }
  @keyframes animate3 {
    0%{stroke-dashoffset:471.24}
    100%{stroke-dashoffset:1413.72}
  }
  @keyframes animate4 {
    0%{stroke-dashoffset:706.86}
    100%{stroke-dashoffset:1649.34}
  }
  #animate1{
    animation:deform  4s ease  infinite, animate1 2s linear infinite;
    stroke:#ffb850;
  }
  #animate2 {
    animation:deform  4s ease  infinite, animate2 2s linear infinite;
    stroke:#ff7e5d;
  }
  #animate3 {
    animation:deform  4s ease  infinite, animate3 2s linear infinite;
    stroke:#8cd2a4;
  }
  #animate4 {
    animation:deform  4s ease  infinite, animate4 2s linear infinite;
    stroke:#62adea;
  }
  path{
    stroke-linecap:round;
    stroke-linejoin:round;
    stroke-miterlimit:10;
    stroke-width:10;
    /* 这里表示实线和虚线，加起来就是周长 */
    stroke-dasharray:235.62 706.86;
    fill:none
  }
</style>



