<template>
  <div>
    <canvas class="canvas-content" ref="canvas"></canvas>
  </div>
</template>

<script>
export default {
    name: 'OpticalLineComponent',
    props: ['setHead'],
    data () {
        return {
            ctx: '',
            mark: 5,
            mama: {}
        }
    },
    mounted () {
        this.setHead({
            msg: '光线'
        });
        this.initCanvas();
    },
    methods: {
        initCanvas () {
            let canvas = this.$refs.canvas;
            canvas.style.width = document.documentElement.clientWidth+'px';
            canvas.style.height = document.documentElement.clientHeight-49+'px';
            canvas.width = document.documentElement.clientWidth*3;
            canvas.height = (document.documentElement.clientHeight-49)*3;
            canvas.style.background = 'black';
            let ctx = canvas.getContext('2d');
            ctx.scale(3, 3);
            // ctx.translate(0.5,0.5);
            ctx.lineWidth=1;
            ctx.strokeStyle = '#fff';

            ctx.beginPath();
            ctx.closePath();

            let x=document.documentElement.clientWidth/2,y=24,i=34,n=document.documentElement.clientWidth,z=n-i*2,m=Math.sqrt(z*z-(z/2*z/2));
            console.log(z)

            // a:366  b:183 c:?  b:3 c:4 a:5 
            // x*x = 366*366 - 183*183
          
            // this.drawLine2(ctx, {x:n-z,y:m}, {x:x,y:z}, 0.5, ()=>{})

            // 直接用lineTo画线,头部光点没想到怎么加上去
            // this.start1(ctx, x, y, j);
            // this.move(ctx, (300-(300-20*280/(20-x)))*(20-x)/280, 300);
            // this.end(ctx, document.documentElement.clientWidth-20, 300);

            // this.arcLine(ctx, 5);
            // 给定两点用多个点实现线条,最初的想法,可以保证有头部光点,但是缺点线条太粗

            // let self = this;
            // function pro(){
            //     return new Promise((resolve,reject)=>{//创建一个Promise对象,里面一个匿名函数
            //         // setTimeout(resolve,ms,'done');//超过ms秒后调用resolve函数
            //         self.drawLine2(ctx, {x:x,y:z}, {x:z,y:m}, 5.5, resolve);
            //     })
            // }

            let self = this;
            function* main(){
                let arr = [];
                arr.push(self.pro((resolve)=>{
                    self.drawLine2(ctx, {x:x,y:y}, {x:i,y:m+y}, 7.5, resolve)
                }))
                arr.push(self.pro((resolve)=>{
                    self.drawLine2(ctx, {x:i,y:m+y}, {x:n-i,y:m+y}, 7.5, resolve)
                }))
                arr.push(self.pro((resolve)=>{
                    self.drawLine2(ctx, {x:n-i,y:m+y}, {x:x,y:y}, 7.5, resolve)
                }))
                Promise.all(arr).then(()=>{
                    self.mama.next()
                })


                yield
                let arr2 = [];
                arr2.push(self.pro((resolve)=>{
                    self.drawLine2(ctx, {x:i,y:m/3+y}, {x:n-i,y:m/3+y}, 7.5, resolve)
                }))
                arr2.push(self.pro((resolve)=>{
                    self.drawLine2(ctx, {x:n-i,y:m/3+y}, {x:x,y:m+m/3+y}, 7.5, resolve)
                }))
                arr2.push(self.pro((resolve)=>{
                    self.drawLine2(ctx, {x:x,y:m+m/3+y}, {x:i,y:m/3+y}, 7.5, resolve)
                }))
                Promise.all(arr2).then(()=>{self.mama.next()})


                yield
                self.drawArc(ctx, {x:x, y:(m+m/3)/2+y}, m*2/3, 4, ()=>{})
                self.drawArc(ctx, {x:x, y:(m+m/3)/2+y}, m/3, 4, ()=>{}, -1)
            }
            this.mama = main();
            this.mama.next();
            
            // this.drawLine2(ctx, {x:z,y:m-(m-z)*2/3}, {x:n-z,y:m-(m-z)*2/3}, 7.5, ()=>{})
            // this.drawLine2(ctx, {x:n-z,y:m-(m-z)*2/3}, {x:x,y:m+(m-z)/3}, 5.5, ()=>{})
            // this.drawLine2(ctx, {x:x,y:m+(m-z)/3}, {x:z,y:m-(m-z)*2/3}, 7.5, ()=>{})
        },
        /**
         * 给定两点用多个点实现线条,最初的想法,可以保证有头部光点,但是缺点线条太粗
         * @ctx       表示canvas2D对象
         * @start     表示开始坐标{x,y}
         * @end  表示终点坐标{x,y}
         * @speed     表示运行速度
         * @cb        执行完毕回调函数
         * @origin    移动坐标
         */
        drawLine2 (ctx, start, end, speed, cb, origin) {
            // 线条超出部分用于擦除头部
            let mark = 18;
            // 判断xy轴
            let xory = Math.abs(start.x - end.x) >= Math.abs(start.y - end.y);
            // 判断方向-为向上或向前
            let sign = (xory ? start.x - end.x : start.y - end.y) >= 0 ? -1 : 1;
            // 移动距离
            let distance = Math.abs(xory ? start.x - end.x : start.y - end.y);
            // 记录初始位置,用于解决头部光点
            let first = xory ? start.x : start.y;
            let last = xory ? end.x : end.y;
            origin = origin || {x:start.x, y:start.y};
            // 确定要变动的轴
            let point = xory ? origin.x : origin.y;

            // 移动到点回调
            if((point>last+mark && sign>=0) || (point<last-mark && sign<0)){
                ctx.strokeStyle = 'green';
                ctx.moveTo(start.x,start.y);
                ctx.lineTo(end.x,end.y);
                ctx.closePath();
                ctx.stroke();
                cb && cb()
                return;
            }

            requestAnimationFrame(()=>{
                // 将线分为两条,头和尾
                let x,y,i,j,m,n;
                // 头部
                m = Math.abs(point-first) <= mark;
                n = Math.abs(point-first) <= distance;
                if(xory){
                    x = m ? first : point - (mark*sign);
                    y = this.lineFun(start, end, x);
                    i = n ? point : end.x;
                    j = this.lineFun(start, end, i);
                }else if(start.x == end.x){
                    y = m ? first : point - (mark*sign);
                    j = n ? point : end.y;
                    x = i = start.x;
                }else if(start.y == end.y){
                    x = m ? first : point - (mark*sign);
                    i = n ? point : end.x;
                    y = j = start.y;
                }else{
                    y = m ? first : point - (mark*sign);
                    j = n ? point : end.y;
                    x = this.lineFun(start, end, y*-1);
                    i = this.lineFun(start, end, j*-1);
                }

                var gradient = ctx.createLinearGradient(x,y,i,j);
                gradient.addColorStop(0,"green");
                gradient.addColorStop(1,"white");
                ctx.strokeStyle = 'green';
                ctx.moveTo(x,y);
                ctx.lineTo(i,j);
                ctx.closePath();
                ctx.stroke();

                // ctx.strokeStyle = 'green';
                // ctx.moveTo(start.x,start.y);
                // ctx.lineTo(x,y);
                // ctx.closePath();
                // ctx.stroke();

                point += (speed*sign);
                origin[xory?'x':'y'] = point;
                this.drawLine2(ctx, start, end, speed, cb, origin);
            })
        },
        // 返回promise对象
        pro (cb) {
            return new Promise((resolve,reject)=>{//创建一个Promise对象,里面一个匿名函数
                // setTimeout(resolve,ms,'done');//超过ms秒后调用resolve函数
                // self.drawLine2(ctx, {x:x,y:z}, {x:z,y:m}, 5.5, resolve);
                cb && cb(resolve);
            })
        },
        /**
         * 给定中心点和半径画圆 
         * @ctx       表示canvas2D对象
         * @middle    表示开始坐标{x,y}
         * @radius    半径
         * @speed     表示运行速度
         * @cb        执行完毕回调函数
         */
        drawArc (ctx, middle, radius, speed, cb, mark = 1, angle = 1) {
        	if(angle>361){
        		return cb && cb();
        	}
            requestAnimationFrame(()=>{
                ctx.strokeStyle = 'green';
                ctx.beginPath();
                ctx.arc(middle.x, middle.y, radius, -Math.PI*mark/2, Math.PI*(angle-90)*mark/180, mark==-1);
                ctx.stroke();
                
                angle+=speed;
                this.drawArc(ctx, middle, radius, speed, cb, mark, angle);
            })
        },
        // 颜色渐变
        distance (x1, y1, x2, y2) {
            return Math.sqrt(Math.pow(y2 - y1, 2) + Math.pow(x2 - x1, 2))
        },
        // 简单的点排列成线
        arcLine (ctx, s) {
            let pointX = 20, pointY = 400;
            let k = setInterval(()=>{
                if(pointX>=300){
                  clearInterval(k);
                }

                let line = pointX;
                for(let x = 0; x <= pointX - 20; x+=0.5){

                    let c;
                    if(x+20 >= line){
                        // let color = this.distance(0, 0, pointX, pointY);
                        c = 'hsl(120,80%,100%)'
                    }else if(x+20 >= line-3){
                        c = 'hsl(120,80%,90%)'
                    }else if(x+20 >= line-6){
                        c = 'hsl(120,80%,80%)'
                    }else if(x+20 >= line-18){
                        c = 'hsl(120,80%,70%)'
                    }else if(x+20 >= line-24){
                        c = 'hsl(120,80%,60%)'
                    }else if(x+20 >= line-30){
                        c = 'hsl(120,80%,50%)'
                    }else{
                        c = 'hsl(120,800%,50%)';
                    }
                    // console.log(c);
                    ctx.strokeStyle = c;
                    ctx.beginPath();
                    ctx.arc(20+x, pointY, 0.5, 0, Math.PI * 2);
                    ctx.closePath();
                    ctx.stroke();
                }

                ctx.strokeStyle = 'hsl(120,80%,100%)';
                ctx.beginPath();
                ctx.arc(pointX, pointY, 1.5, 0, Math.PI * 2);
                ctx.closePath();
                ctx.stroke();

                pointX += 0.5;
            },0)
        },
        /**
         * 给定两点用多个点实现线条,最初的想法,可以保证有头部光点,但是缺点线条太粗
         * @ctx       表示canvas2D对象
         * @start     表示开始坐标{x,y}
         * @end  表示终点坐标{x,y}
         * @speed     表示运行速度
         * @cb        执行完毕回调函数
         * @origin    移动坐标
         */
        drawLine (ctx, start, end, speed, cb, origin) {
            // 线条超出部分用于擦除头部
            let mark = 18;
            // 判断xy轴
            let xory = Math.abs(start.x - end.x) >= Math.abs(start.y - end.y);
            // 判断方向-为向上或向前
            let sign = (xory ? start.x - end.x : start.y - end.y) >= 0 ? -1 : 1;
            // 移动距离
            let distance = Math.abs(xory ? start.x - end.x : start.y - end.y);
            // 记录初始位置,用于解决头部光点
            let first = xory ? start.x : start.y;
            // 最后一步距离
            let tail = first + (distance + mark)*sign - (distance+mark)%speed - speed;
            origin = origin || {x:start.x, y:start.y};
            // 确定要变动的轴
            let point = xory ? origin.x : origin.y;
            // 保存初始值
            // origin = origin || point;
            // 完成返回满足移动完成距离+超出
            if(point>first+distance+mark && sign>=0){
                ctx.strokeStyle = 'hsl(120,800%,50%)';
                ctx.beginPath();
                ctx.arc(end.x, end.y, 0.5, 0, Math.PI * 2);
                ctx.closePath();
                ctx.stroke();
                cb && cb();
                return;
            }
            if(point<first-distance-mark && sign<0){
                ctx.strokeStyle = 'hsl(120,800%,50%)';
                ctx.beginPath();
                ctx.arc(end.x, end.y, 0.5, 0, Math.PI * 2);
                ctx.closePath();
                ctx.stroke();
                cb && cb();
                return;
            }
            // 执行动画
            requestAnimationFrame(()=>{
                // 记录当前点用于
                // let line = start.x;
                // 将矩形覆盖线条擦除头部圆点
                // if(start.x<=start.x+speed+mark){
                //   ctx.strokeStyle = 'black';
                //   ctx.fillRect(18,398, x+1, 4);
                // }300,200 160,20 2

                // 将之前已经画的点重新画,用来擦除头部颜色
                for(let i = 0; i <= Math.abs(point - first); i+=0.5){
                    let c,j = i*sign;
                    // 头部颜色
                    if(sign>0){
                        if(j+first > first+distance){
                            c = 'hsl(120,80%,0%)'
                        }else if(j+first >= point){
                            c = 'hsl(120,80%,100%)'
                        }else if(j+first >= point-mark/6){
                            c = 'hsl(120,80%,90%)'
                        }else if(j+first >= point-mark/3){
                            c = 'hsl(120,80%,75%)'
                        }else if(j+first >= point-mark){
                            c = 'hsl(120,80%,60%)'
                        }else{
                            c = 'hsl(120,800%,50%)';
                        }
                    }else{
                        if(j < distance*sign){
                            c = 'hsl(120,80%,0%)'
                        }else if(j+first <= point){
                            c = 'hsl(120,80%,100%)'
                        }else if(j+first <= point-mark/6){
                            c = 'hsl(120,80%,90%)'
                        }else if(j+first <= point-mark/3){
                            c = 'hsl(120,80%,75%)'
                        }else if(j+first <= point-mark){
                            c = 'hsl(120,80%,60%)'
                        }else{
                            c = 'hsl(120,800%,50%)';
                        }
                    }
                    // 开始画点
                    ctx.strokeStyle = c;

                    ctx.beginPath();
                    let x,y;
                    if(xory){
                        x = first+j
                        y = this.lineFun(start, end, x);
                    }else if(start.x == end.x){
                        y = first+j;
                        x = start.x;
                    }else if(start.y == end.y){
                        x = first+j;
                        y = start.y;
                    }else{
                        y = first+j;
                        x = this.lineFun(start, end, y*-1);
                    }
                    ctx.arc(x, y, 0.5, 0, Math.PI * 2);
                    ctx.closePath();
                    ctx.stroke();
                }
                // if(x<300){
                //   ctx.strokeStyle = 'hsl(120,80%,100%)';
                //   ctx.beginPath();
                //   ctx.arc(x, y, 1, 0, Math.PI * 2);
                //   ctx.closePath();
                //   ctx.stroke();
                // }

                point += (speed*sign);
                origin[xory?'x':'y'] = point;
                this.drawLine(ctx, start, end, speed, cb, origin);
            })
        },
        // 指定两点算线
        lineFun (start, end, mark) {
            let x,y,x1 = start.x,y1 = start.y,x2 = end.x,y2 = end.y;
            if(mark < 0){
                return (Math.abs(mark)-(y1 - x1*(y1 - y2)/(x1 - x2)))*(x1 - x2)/(y1 - y2)
            }else{
                return Math.abs(mark)*(y1 - y2)/(x1 - x2) + (y1 - x1*(y1 - y2)/(x1 - x2))
            }
        },
        // 直接用lineTo画线,头部光点没想到怎么加上去,左线
        start1 (ctx, x, y, j) {
            let key = setInterval(()=>{
                // let j = -2*i+320;
                j = j >= 300 ? 300 : j;
                let i = (j-(300-20*280/(20-x)))*(20-x)/280;
                if(j<=300){
                    ctx.strokeStyle = 'white';
                    ctx.moveTo(x,y);
                    ctx.lineTo(i,j);
                    ctx.closePath();
                    ctx.stroke();
                }
                if(j == 300){
                    clearInterval(key);
                }
                j+=7.5;
            },50)
        },
        // 下直线
        move (ctx, i, j) {
            let key = setInterval(()=>{
                i = i >= document.documentElement.clientWidth-20 ? document.documentElement.clientWidth-20 : i;
                if(i==document.documentElement.clientWidth-20){
                    clearInterval(key);
                    // this.end(ctx, i, j);
                }
                ctx.strokeStyle = 'white';
                ctx.moveTo(20,j);
                ctx.lineTo(i,j);
                ctx.closePath();
                ctx.stroke();
                i+=10;
            },50)
        },
        // 右线
        end (ctx, x, y) {
            let i = x;
            let key = setInterval(()=>{
                let w = document.documentElement.clientWidth;
                i = i <= w/2 ? w/2 : i;
                let j = i * (280/((w-20) - (w/2))) + 20 - w/2*(280/((w-20) - (w/2)));
                if(i==w/2){
                    clearInterval(key);
                }
                ctx.strokeStyle = 'white';
                ctx.moveTo(x ,y);
                ctx.lineTo(i,j);
                ctx.closePath();
                ctx.stroke();
                i-=5;
            },50)
        }
    }
}
</script>

