<template>
  <div class="canvas-main">
    <header @click="back">{{obj.title}}</header>
    <div v-if="obj.description" v-pre-code="{description:obj.description,deCode:deCode}"></div>
    <div v-if="obj.param">
      <div class="canvas-param-header">相关参数</div>
      <div class="canvas-param" v-for="(param, key) in obj.param">
        <p class="canvas-param-key">{{key}}</p>
        <div v-if="param.description" v-pre-code="{description:param.description,deCode:deCode}"></div>
        <ul class="ml-10">
          <li v-for="(o, k) in param" v-show="k != 'description'">
            <p class="canvas-tomato"><i class="canvas-list-mark">●</i>{{k}}</p>
            <p>{{o}}</p>
          </li>
        </ul>
      </div>
    </div>
    <ul class="canvas-fun" v-for="f in obj.function">
      <li @click="next(f.name)">
        <p class="canvas-fun-a">{{f.name}}</p>
        <div v-if="f.description" v-pre-code="{description:f.description,deCode:deCode}"></div>
      </li>
    </ul>
    <div class="canvas-item" v-for="(funs, name) in obj.groupFun" v-if="obj.groupFun">
      <div>{{name}}</div>
      <ul class="canvas-fun" v-for="f in funs">
        <li @click="next(f.name)">
          <p class="canvas-fun-a">{{f.name}}</p>
          <div v-if="f.description" v-pre-code="{description:f.description,deCode:deCode}"></div>
        </li>
      </ul>
    </div>
    <p>
      <button @click="showJson">123</button>
      <button @click="start" id="button">执行</button>
      <select class="canvas-select" v-model="caseCode" v-if="obj.caseCode">
        <option v-for="(c, k) in obj.caseCode" :value="c">{{k}}</option>
      </select>
    </p>
    <textarea ref="textarea" class="canvas-input">{{caseCode}}</textarea>
    <canvas ref="picture" id="picture" class="canvas-run"></canvas>
  </div>
</template>

<script>
import _ from "underscore"
import dataJson from '@/js/canvasJson'
export default {
  name: 'CanvasComponent',
  props: ['setHead'],
  data () {
    return {
      obj: {},
      deCode: [],
      caseCode: ''
    }
  },
  mounted () {
    this.obj = dataJson['HTMLCanvasElement'];
    this.deCode = _.clone(dataJson['HTMLCanvasElement'].deCode, {});
    this.setHead({
      msg: 'Canvas Api'
    });
  },
  methods: {
    // 输出JSON下格式
    showJson () {
      console.log(JSON.stringify(this.$refs.textarea.value));
    },
    // 返回上一级
    back () {
      // 从title找上一级
      let t = this.obj.title.split('.')[0];
      t = (t == this.obj.title) ? 'HTMLCanvasElement' : t;
      // 清除输入和canvas
      this.caseCode = '';
      let cxt=this.$refs.picture.getContext("2d");
      this.$refs.picture.height = 150;
      this.next(t);
    },
    // 进入下一级
    next (k) {
      this.obj = dataJson[k];
      // 用于显示代码,防止重复
      this.deCode = _.clone(dataJson[k].deCode, {});
      let d = this.obj.description;
      this.obj.description = '';
      this.caseCode = this.obj.caseCode && this.obj.caseCode[Object.keys(this.obj.caseCode)[0]];
      // 强行更新说明文案的指令
      this.$nextTick(()=>{
        this.obj.description = d;
        this.start();
      })
    },
    // 开始执行输入代码
    start () {
      // 刷新canvas
      this.$refs.picture.height = 150;
      let canvas = document.getElementById("picture");
      let ctx = canvas.getContext("2d");
      eval(this.$refs.textarea.value);
    }
  },
  directives: {
    // 说明文案中插入代码
    preCode: {
      inserted: function(el, binding){
        let list = binding.value.description.split('<deCode>');
        let template = '';
        for(let i in list){
          list[i] = list[i].replace(/</g, '&lt;');
          template += ('<p>'+list[i]+'</p>');
          if(i < list.length-1){
            template += ('<pre class="canvas-pre">' + binding.value.deCode.pop().replace(/</g, '&lt;') +'</pre>');
          }
        }
        el.innerHTML = template;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .canvas-main{
    width: 100%;
    padding: .1rem;
    box-sizing: border-box;
    text-align: left;
  }
  .canvas-main header,.canvas-param-header{
    color: #333;
    font-size: .20rem;
    font-weight: bold;
  }
  .canvas-pre{
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: .1rem 0;
    background: antiquewhite;
    padding: .1rem;
  }
  .canvas-fun li{
    margin: .1rem 0;
  }
  .canvas-fun-a{
    color: dodgerblue;
    text-decoration-line: underline;
  }
  .canvas-param-key{
    color: #43b1d4;
    font-size: .18rem;
  }
  .canvas-list-mark{
    font-style: inherit;
    font-size: .12rem;
    float: left;
    margin-left: -.1rem;
  }
  .canvas-tomato{
    color: tomato;
  }
  .canvas-input{
    width: 100%;
    height: 2rem;
    resize: none;
    padding: 0;
  }
  .canvas-item{
    border-top: 2px solid #58a5ce;
    padding-top: .05rem;
    margin-top: .05rem;
  }
  .canvas-run{
    width: 100%;
  }
</style>



